import { __ } from '@wordpress/i18n';
import { Modal } from '@wordpress/components';

const ProModal = ({ proModalOpen, setProModalOpen }) => {

    return (
        <>
            {proModalOpen && (
                <Modal className='ttpProModal' title={__('Pro Feature', 'slider')} onRequestClose={() => setProModalOpen(false)}>

                    <div className='bsbProFeature'>
                        <h3>{__('Please', 'slider')}<a target="_blank" href="https://bplugins.com/products/b-slider/#pricing" rel="noreferrer"> {__('Upgrade To PRO', 'slider')}</a>{__('  To Unlock Premium Features', 'slider')}</h3>
                        <a href="https://bplugins.com/products/b-slider/#pricing" target="_blank" rel="noreferrer" >{__('Upgrade Now', 'slider')}</a>
                    </div>
                </Modal>
            )}
        </>
    );
};

export default ProModal;