


import { __ } from '@wordpress/i18n';
import { useEffect, useState } from '@wordpress/element';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextareaControl, Button, TabPanel, SelectControl, TextControl, __experimentalUnitControl as UnitControl, __experimentalBoxControl as BoxControl, __experimentalNumberControl as NumberControl, ToggleControl, RangeControl, __experimentalBorderControl as BorderControl, PanelRow, __experimentalAlignmentMatrixControl as AlignmentMatrixControl, FocalPointPicker } from "@wordpress/components";
import produce from 'immer';
import patterns from './../src/patterns.json';

// Custom Components
import { InlineDetailMediaUpload, ColorsControl, BDevice, Typography, BtnGroup, BColor, Label, HelpPanel } from '../../Components';

import ProModal from './ProModal';

import { pxUnit, vhUnit, emUnit, perUnit, tabs, styles, indicatorOption, sliderOption, animationOptions, contentAniOption, animationFreeOptions, contentPosition, arrowStyles, indicatorOptions, titleTag } from './Const/options';
import icons from './Const/icons';

import { BControlPro } from '../bplugins_sdk/src/components/v1';
import ButtonGroupController from './ButtonGroupController';
import { parse } from '@wordpress/blocks';
import { checkDirection } from './Const/functions';

const Settings = (props) => {

    const { attributes, setAttributes, updateSlider, addSlider, removeSlider, duplicateSlider, isBackend, custom = {} } = props;
    const { sliders, title, titleFCaption, slideInnerGap, titleTypo, titleColor, descTypo, descColor, titleMargin, descMargin, arrow, indicator, SliderOverly, options, height, sliderHeight, slideInnerGapDevice, borderRadius, margin, animation, position, arrowWidth, deviceArrowWidth, arrowHeight, deviceArrowHeight, arrowRadius, arrowStyle, btnColors, btnHovColors, btnTypo, btnPadding, btnBorder, btnRadius, direction, isPremium, titleAnimation, descAnimation, btnAnimation, isMouseWheel, isArrowFollowSlide, isMouseDrag, isLazyLoad } = attributes;

    const [proModalOpen, setProModalOpen] = useState(false);
    const [device, setDevice] = useState('desktop');
    const [gapDevice, setGapDevice] = useState('desktop');
    const [DArrowWidth, setDArrowWidth] = useState('desktop');
    const [DArrowHeight, setDArrowHeight] = useState('desktop');

    // const [focalPoint, setFocalPoint] = useState({
    //     x: 0.5,
    //     y: 0.5,
    // });

    const updateObject = (attr, key, val) => {
        const newAttr = { ...attributes[attr] };
        newAttr[key] = val;
        setAttributes({ [attr]: newAttr })
    }

    // const url = "https://images.unsplash.com/photo-1621155346337-1d19476ba7d6?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D";

    /* Example function to render the CSS styles based on Focal Point Picker value */
    // const style = {
    //     backgroundImage: `url(${url})`,
    //     backgroundPosition: `${focalPoint.x * 100}% ${focalPoint.y * 100}%`,
    // };

    return <> <InspectorControls style={{ marginBottom: "40px" }}>
        <TabPanel className="bPlTabPanel bsb-tab-panel" activeClass="activeTab" tabs={tabs}>
            {(tab) => <>
                {'General' == tab.name && <>
                    <HelpPanel slug="b-slider" docsLink="https://bplugins.com/docs/b-slider" />

                    <PanelBody className='bPlPanelBody' title={__('Slides', 'slider')}>
                        {sliders?.map((slider, index) => {
                            const { title, img, desc, btnLabel, btnUrl, target } = slider;
                            return <PanelBody className='bPlPanelBody bsbGeneralMainArea' key={index} title={`Slide ${index + 1}`} initialOpen={0 === index}>

                                <div key={index} className="components_button_main" onClick={() => isBackend ? custom.setActiveIndex(index) : null}>
                                    <Label>{__('Slide Image:', 'slider')}</Label>

                                    <InlineDetailMediaUpload value={img} type={['image']} onChange={val => updateSlider('img', index, val)} placeholder={__('Enter Image URL', 'slider')} />

                                    <TextControl label={__("Title", 'slider')} labelPosition={__('top', 'slider')} value={title != null ? title : img?.caption || img?.title} placeholder={__('Enter Title', 'slider')} onChange={val => updateSlider('title', index, val)} />

                                    <TextareaControl label={__("Description", 'slider')} labelPosition={__('top', 'slider')} value={desc} placeholder={__('Enter desc', 'slider')} onChange={val => updateSlider('desc', index, val)} />

                                    <BControlPro label={__("Button label", 'slider')} labelPosition={__('top', 'slider')} value={btnLabel} placeholder={__('Enter Label', 'slider')} onChange={val => updateSlider('btnLabel', index, val)} isPremium={isPremium} Component={TextControl} setOpen={setProModalOpen} />

                                    <BControlPro label={__("Button Url", 'slider')} labelPosition={__('top', 'slider')} value={btnUrl} placeholder={__('Enter Url', 'slider')} onChange={val => updateSlider('btnUrl', index, val)} isPremium={isPremium} Component={TextControl} setOpen={setProModalOpen} />

                                    <BControlPro className='mt20' label={__('Open in new tab', 'slider')} checked={target} onChange={(val) => updateSlider('target', index, val)} isPremium={isPremium} Component={ToggleControl} setOpen={setProModalOpen} />

                                    <div className="itemAction bsbButtonArea mt20">
                                        {1 < sliders.length && <Button className='removeItem bsb-general-button' onClick={() => removeSlider(index)}>{__('Remove', 'slider')}</Button>}

                                        <Button className='duplicateItem bsb-general-button' onClick={() => duplicateSlider(index)}>{__('Duplicate', 'slider')}</Button>
                                    </div>
                                </div>

                            </PanelBody>
                        })}

                        {/* Slide Add Button  */}
                        <div className='addItem'>
                            <Button className='bsb-general-button' onClick={addSlider}>{__('Add New Slide', 'slider')}</Button>
                        </div>
                    </PanelBody>

                    <PanelBody className='bPlPanelBody' title={__('Title', 'slider')} initialOpen={false}>
                        <BControlPro label={__('Tag', 'Slider')} options={titleTag} labelPosition='left' value={title?.tag || 'h5'} onChange={val => updateObject('title', 'tag', val)} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                        <ToggleControl className='mt10' label={__('Import Title From Media Caption', 'slider')} checked={titleFCaption} onChange={(val) => setAttributes({ titleFCaption: val })} />
                    </PanelBody>

                    <PanelBody className='' title={__('Layout Settings', '')} initialOpen={false}>

                        {/* <BControlPro className='mb20' label={__('Left/Right Inner Gap', 'slider')} labelPosition='left' value={slideInnerGap} onChange={(val) => {
                            setAttributes({ slideInnerGap: val });
                        }} units={[pxUnit(100), perUnit(10)]} isResetValueOnUnitChange={true} isPremium={isPremium} Component={UnitControl} setOpen={setProModalOpen} /> */}

                        <PanelRow className='mt20'>
                            <Label mt='0'>{__('Gap:', 'slider')}</Label>
                            <BControlPro device={gapDevice} onChange={val => setGapDevice(val)} isPremium={isPremium} Component={BDevice} setOpen={setProModalOpen} />
                        </PanelRow>

                        <BControlPro className='mb20' label={__('Left/Right Inner Gap', 'slider')} labelPosition='left' value={slideInnerGapDevice[gapDevice] || slideInnerGap} onChange={val => { setAttributes({ slideInnerGapDevice: { ...slideInnerGapDevice, [gapDevice]: val } }) }} units={[pxUnit(400), vhUnit(30)]} isResetValueOnUnitChange={true} beforeIcon='grid-view' isPremium={isPremium} Component={UnitControl} setOpen={setProModalOpen} />

                        <BControlPro className='mt20' label={__('Slide Direction', 'slider')} labelPosition='left' value={direction} options={sliderOption} onChange={(val) => { setAttributes({ direction: val }) }} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                        {/* Height define option  */}
                        <PanelRow className='mt20'>
                            <Label mt='0'>{__('Height:', 'slider')}</Label>
                            <BControlPro device={device} onChange={val => setDevice(val)} isPremium={isPremium} Component={BDevice} setOpen={setProModalOpen} />
                        </PanelRow>

                        <UnitControl className='mb20' label={__('Slider Height', 'slider')} labelPosition='left' value={sliderHeight[device] || height} onChange={val => { setAttributes({ sliderHeight: { ...sliderHeight, [device]: val } }) }} units={[pxUnit(400), vhUnit(30)]} isResetValueOnUnitChange={true} beforeIcon='grid-view' />

                        {/* <>
                            <FocalPointPicker
                                url={url}
                                value={focalPoint}
                                onDragStart={setFocalPoint}
                                onDrag={setFocalPoint}
                                onChange={setFocalPoint}
                            />
                            <div style={style} />
                        </> */}


                        <PanelRow>
                            {isPremium ?
                                <><Label>{__('Content Position:', 'slider')}</Label>
                                    <AlignmentMatrixControl value={position?.top || position} onChange={val => setAttributes({ position: val })} />
                                </> :
                                <SelectControl label={__('Content Position:', 'slider')} labelPosition='left' value={position} options={contentPosition} onChange={(val) => { setAttributes({ position: val }) }} />
                            }
                        </PanelRow>

                        <BControlPro label={__('Arrow Style:', 'slider')} value={arrowStyle} onChange={(arrowStyle) => setAttributes({ arrowStyle })} isPremium={isPremium} setOpen={setProModalOpen} Component={ButtonGroupController} options={arrowStyles} />
                    </PanelBody>

                    <PanelBody className='' title={__('Slider Options', 'slider')} initialOpen={false}>

                        {isPremium ?
                            <SelectControl label={__('Animation/Effect:', 'slider')} labelPosition='side' value={animation} onChange={(val) => setAttributes({ animation: val })} options={animationOptions} />
                            :
                            <>
                                <PanelRow>
                                    <Label className='mb0'>{__('Animation/Effect:', 'slider')}</Label>
                                    <BtnGroup value={animation} onChange={val => setAttributes({ animation: val })} options={animationFreeOptions} />
                                </PanelRow>
                            </>
                        }

                        <ToggleControl className='mt20' label={__('Autoplay', 'slider')} checked={options.ride} onChange={(value) => updateObject('options', 'ride', value)} />

                        {options.ride && <>
                            <NumberControl className='mb10' label={__('Interval(ms)', 'slider')} labelPosition='left' value={options.interval} onChange={(value) => updateObject('options', 'interval', parseInt(value))} min={1000} max={10000} />

                            <ToggleControl className='mt10' label={__('Pause on Mouse over', 'slider')} checked={options.pause} onChange={(value) => updateObject('options', 'pause', value)} />

                            {/* <ToggleControl className='mt10' label={__('Random Slide', 'slider')} checked={options?.shuffle} onChange={(value) => updateObject('options', 'shuffle', value)} /> */}
                        </>}

                        <ToggleControl className='mt20' label={__('Show Arrow/Navigation', 'slider')} checked={arrow.visibility} onChange={(value) => { updateObject('arrow', 'visibility', value) }} />

                        <ToggleControl className='mt10' label={__('Show Indicators/Pagination', 'slider')} checked={indicator.visibility} onChange={(value) => updateObject('indicator', 'visibility', value)} />

                        <BControlPro className='mt20' label={__('Slide On MouseWheel', 'slider')} checked={isMouseWheel} onChange={(val) => setAttributes({ isMouseWheel: val })} isPremium={isPremium} Component={ToggleControl} setOpen={setProModalOpen} />

                        <BControlPro className='mt10 marginLeft' label={__('Arrow Follow Mouse', 'slider')} checked={isArrowFollowSlide} onChange={(val) => setAttributes({ isArrowFollowSlide: val })} isPremium={isPremium} Component={ToggleControl} setOpen={setProModalOpen} />
                        <small className='warning'>{icons.warning}Only work frontEnd and button will not work</small>

                        <BControlPro className='mt10 marginLeft' label={__('Slide on Mouse Drag', 'slider')} checked={isMouseDrag} onChange={(val) => setAttributes({ isMouseDrag: val })} isPremium={isPremium} Component={ToggleControl} setOpen={setProModalOpen} />
                        <small className='warning'>Arrow Follow Mouse enable slide on mouse drag not working</small>

                        <BControlPro className='mt20' label={__('Lazy Load Enable', 'slider')} checked={isLazyLoad} onChange={(val) => setAttributes({ isLazyLoad: val })} isPremium={isPremium} Component={ToggleControl} setOpen={setProModalOpen} />
                    </PanelBody>

                    {indicator.visibility && <PanelBody className='bPlPanelBody' title={__('Indicators', 'slider')} initialOpen={false}>
                        <BControlPro className='mt10' label={__('Type', 'slider')} value={indicator?.type} labelPosition='left' onChange={(val) => setAttributes({
                            indicator: {
                                ...indicator,
                                type: val,
                                width: 'image' === val ? '80px' : '30px',
                                height: 'image' === val ? '80px' : '3px',
                                radius: 'image' === val ? '50%' : '0px',
                                moveFromEdge: 'image' === val ? '50%' : '-15px',
                            }
                        })} options={indicatorOption} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                        {isPremium && <PanelRow className='mt20'>
                            <Label className='mb0'>{__('Position:', 'slider')}</Label>
                            <AlignmentMatrixControl value={indicator.position}
                                onChange={val => {
                                    setAttributes({
                                        indicator: { ...indicator, position: val, ...checkDirection(val) },

                                    })
                                }}

                            />
                        </PanelRow>}

                        <BControlPro label={__('Direction', 'slider')} labelPosition='side' value={indicator.direction} onChange={(val) => { updateObject('indicator', 'direction', val) }} options={indicatorOptions} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                        {
                            indicator.position !== 'center center' && <>
                                <BControlPro className='mb20 mt20 marginLeft' label={__('Move From Edge', 'slider')} labelPosition='left' value={indicator.moveFromEdge} onChange={(val) => updateObject('indicator', 'moveFromEdge', val)} units={[pxUnit(40), perUnit(50)]} isResetValueOnUnitChange={true} isPremium={isPremium} Component={UnitControl} setOpen={setProModalOpen} />
                                <small className='warning'>{__('Use a negative or positive value to move in or out.', 'slider')}</small>
                            </>
                        }

                    </PanelBody>}
                </>}

                {'style' == tab.name && <>
                    <div className='bsbGeneralMainArea'>
                        <PanelBody className='' title={__('Slider', 'slider')} initialOpen={false}>
                            <BColor className='mb20' label={__('Overly Color', 'slider')} value={SliderOverly} defaultColor="#59595952" onChange={(val) => { setAttributes({ SliderOverly: val }) }} />

                            <BControlPro className='mt20' label={__('Margin', 'slider')} values={margin} onChange={val => setAttributes({ margin: val })} resetValues={{ top: '0px', right: '0px', bottom: '0px', left: '0px' }} units={[pxUnit(3)]} isPremium={isPremium} Component={BoxControl} setOpen={setProModalOpen} />

                            <BoxControl className='mt20' label={__('Border Radius', 'slider')} values={borderRadius} onChange={val => setAttributes({ borderRadius: val })} resetValues={{ top: '0px', right: '0px', bottom: '0px', left: '0px' }} units={[pxUnit(3), emUnit(2)]} />
                        </PanelBody>

                        <PanelBody className='' title={__('Title', 'slider')} initialOpen={false}>
                            <Typography className='mt20' label={__('Typography:', 'slider')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: 25 }} produce={produce} />

                            <BColor label={__('Color', 'slider')} value={titleColor} defaultColor="#fff" onChange={val => setAttributes({ titleColor: val })} />

                            <BoxControl label={__('Padding', 'slider')} values={titleMargin} onChange={val => setAttributes({ titleMargin: val })} resetValues={{ top: '0px', left: '0px', right: '0px', bottom: '0px' }} />

                            <BControlPro className='mt20' label={__('Animation', 'slider')} value={titleAnimation?.effect} labelPosition='left' onChange={(val) => { updateObject('titleAnimation', 'effect', val) }} options={contentAniOption} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                            <BControlPro className='mb10 marginLeft' label={__('Delay(s)', 'slider')} labelPosition='left' value={titleAnimation?.delay} onChange={(val) => updateObject('titleAnimation', 'delay', parseFloat(val))} min={0.0} max={10} step={0.1} isPremium={isPremium} Component={NumberControl} setOpen={setProModalOpen} />

                            <BControlPro className='mb10 mt20 marginLeft' label={__('Duration(s)', 'slider')} labelPosition='left' value={titleAnimation?.duration} onChange={(val) => updateObject('titleAnimation', 'duration', parseFloat(val))} min={0.0} max={10} step={0.1} isPremium={isPremium} Component={NumberControl} setOpen={setProModalOpen} />
                        </PanelBody>

                        <PanelBody className='' title={__('Description', 'slider')} initialOpen={false}>
                            <Typography className='mt20' label={__('Typography:', 'slider')} value={descTypo} onChange={val => setAttributes({ descTypo: val })} defaults={{ fontSize: 25 }} produce={produce} />

                            <BColor label={__('Color', 'slider')} value={descColor} defaultColor="#000" onChange={val => { setAttributes({ descColor: val }) }} />

                            <BoxControl label={__("Margin", 'slider')} values={descMargin} onChange={val => setAttributes({ descMargin: val })} resetValues={{ top: '0px', left: '0px', right: '0px', bottom: '0px' }} />

                            <BControlPro className='mt20' label={__('Animation', 'slider')} value={descAnimation?.effect} labelPosition='left' onChange={(val) => {
                                updateObject('descAnimation', 'effect', val)
                            }} options={contentAniOption} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                            <BControlPro className='mb10 marginLeft' label={__('Delay(s)', 'slider')} labelPosition='left' value={descAnimation?.delay} onChange={(val) => updateObject('descAnimation', 'delay', parseFloat(val))} min={0.0} max={10} step={0.1} isPremium={isPremium} Component={NumberControl} setOpen={setProModalOpen} />

                            <BControlPro className='mb10 mt20 marginLeft' label={__('Duration(s)', 'slider')} labelPosition='left' value={descAnimation?.duration} onChange={(val) => updateObject('descAnimation', 'duration', parseFloat(val))} min={0.0} max={10} step={0.1} isPremium={isPremium} Component={NumberControl} setOpen={setProModalOpen} />
                        </PanelBody>

                        <PanelBody className='' title={__('Button', 'slider')} initialOpen={false}>

                            <BControlPro className='mt20' label={__('Typography:', 'slider')} value={btnTypo} onChange={val => setAttributes({ btnTypo: val })} defaults={{ fontSize: 14 }} isPremium={isPremium} Component={Typography} setOpen={setProModalOpen} produce={produce} />

                            <BControlPro className='' label={__('Colors', 'slider')} value={btnColors} onChange={val => setAttributes({ btnColors: val })} defaults={{ color: '#fff', bg: '#ff3b5c' }} isPremium={isPremium} Component={ColorsControl} setOpen={setProModalOpen} />

                            <BControlPro className='' label={__('Hover Colors', 'slider')} value={btnHovColors} onChange={val => setAttributes({ btnHovColors: val })} defaults={{ color: '#000', bg: '#fff' }} isPremium={isPremium} Component={ColorsControl} setOpen={setProModalOpen} />

                            <BControlPro label={__('Padding', 'slider')} values={btnPadding} onChange={val => setAttributes({ btnPadding: val })} resetValues={{
                                top: '0px', left: '0px', right: '0px', bottom: '0px'
                            }} isPremium={isPremium} Component={BoxControl} setOpen={setProModalOpen} />

                            <BControlPro className='mt10' label={__('Border', 'business-review')} value={btnBorder} onChange={(val) => setAttributes({ btnBorder: val })} isPremium={isPremium} Component={BorderControl} setOpen={setProModalOpen} />

                            <BControlPro className='mt20' label={__('Radius:', 'business-review')} labelPosition='left' value={btnRadius} onChange={val => setAttributes({ btnRadius: val })} units={[pxUnit(10), perUnit(100)]} isResetValueOnUnitChange={true} isPremium={isPremium} Component={UnitControl} setOpen={setProModalOpen} />

                            <BControlPro className='mt20' label={__('Animation', 'slider')} value={btnAnimation?.effect} labelPosition='left' onChange={(val) => {
                                updateObject('btnAnimation', 'effect', val)
                            }} options={contentAniOption} isPremium={isPremium} Component={SelectControl} setOpen={setProModalOpen} />

                            <BControlPro className='mb10 marginLeft' label={__('Delay(s)', 'slider')} labelPosition='left' value={btnAnimation?.delay} onChange={(val) => updateObject('btnAnimation', 'delay', parseFloat(val))} min={0.0} max={10} step={0.1} isPremium={isPremium} Component={NumberControl} setOpen={setProModalOpen} />

                            <BControlPro className='mb10 mt20 marginLeft' label={__('Duration(s)', 'slider')} labelPosition='left' value={btnAnimation?.duration} onChange={(val) => updateObject('btnAnimation', 'duration', parseFloat(val))} min={0.0} max={10} step={0.1} isPremium={isPremium} Component={NumberControl} setOpen={setProModalOpen} />
                        </PanelBody>

                        {arrow.visibility && <PanelBody className='' title={__('Arrow', 'slider')} initialOpen={false}>
                            <RangeControl className='mt20' label={__('Icon Size', 'slider')} value={arrow.size} onChange={(value) => {
                                updateObject('arrow', 'size', value)
                            }} min={1} max={100} />

                            <BColor className='mt20' label={__('Color', 'slider')} value={arrow.color} defaultColor="#000" onChange={val => updateObject('arrow', 'color', val)} />

                            {/* <UnitControl className='mt20' label={__('Width', 'slider')} labelPosition='left' value={arrowWidth} onChange={(val) => { setAttributes({ arrowWidth: val }) }} units={[pxUnit(50), perUnit(25)]} /> */}

                            <PanelRow className='mt0'>
                                <p></p>
                                <BControlPro device={DArrowWidth} onChange={val => setDArrowWidth(val)} isPremium={isPremium} Component={BDevice} setOpen={setProModalOpen} />
                            </PanelRow>

                            <UnitControl className='mb0' label={__('Width', 'slider')} labelPosition='left' value={deviceArrowWidth[DArrowWidth] || arrowWidth} onChange={val => { setAttributes({ deviceArrowWidth: { ...deviceArrowWidth, [DArrowWidth]: val } }) }} units={[pxUnit(400), vhUnit(30)]} isResetValueOnUnitChange={true} beforeIcon='grid-view' />


                            {/* <UnitControl className='mt20' label={__('Height', 'slider')} labelPosition='left' value={arrowHeight} onChange={(val) => { setAttributes({ arrowHeight: val }) }} units={[pxUnit(50), perUnit(25)]} /> */}

                            <PanelRow className=''>
                                <p></p>
                                <BControlPro device={DArrowHeight} onChange={val => setDArrowHeight(val)} isPremium={isPremium} Component={BDevice} setOpen={setProModalOpen} />
                            </PanelRow>

                            <UnitControl className='' label={__('Height', 'slider')} labelPosition='left' value={deviceArrowHeight[DArrowHeight] || arrowHeight} onChange={val => { setAttributes({ deviceArrowHeight: { ...deviceArrowHeight, [DArrowHeight]: val } }) }} units={[pxUnit(400), vhUnit(30)]} isResetValueOnUnitChange={true} beforeIcon='grid-view' />

                            <ColorsControl className='mt10 mb10' label={__('Background', 'slider')} value={arrow}
                                onChange={val => setAttributes({ arrow: { ...arrow, ...val } })} defaults={{ bg: 'transparent' }} isColor={false} />

                            <BoxControl className='mt20' label={__("Border Radius", 'slider')} values={arrowRadius} onChange={val => setAttributes({ arrowRadius: val })} resetValues={{ top: '0px', left: '0px', right: '0px', bottom: '0px' }} />

                        </PanelBody>}

                        {indicator.visibility && <PanelBody className='' title={__('Indicators', 'slider')} initialOpen={false}>
                            {indicator?.type !== 'image' && <>
                                <SelectControl className='mt20' label={__('Style', 'slider')} labelPosition='left' value={indicator.style}
                                    onChange={(val) => {
                                        setAttributes({
                                            indicator: {
                                                ...indicator,
                                                style: val,
                                                width: 'dot' === val ? '10px' : '30px',
                                                height: 'dot' === val ? '10px' : '3px',
                                                radius: 'dot' === val ? '50%' : '0px'
                                            }
                                        });
                                    }} options={styles}
                                    __nextHasNoMarginBottom
                                />

                                <BColor label={__('Color', 'slider')} value={indicator.color} defaultColor="#000" onChange={(val) => updateObject('indicator', 'color', val)} />

                                <BColor label={__('Active Color', 'slider')} value={indicator.activeColor} defaultColor="#000" onChange={(val) => updateObject('indicator', 'activeColor', val)} />
                            </>}

                            <UnitControl label={__('Width', 'slider')} labelPosition='left' value={indicator?.width} onChange={(val) => updateObject('indicator', 'width', val)} units={[pxUnit(10), emUnit(1)]} isResetValueOnUnitChange={true} />

                            <UnitControl className='mt20' label={__('Height', 'slider')} labelPosition='left' value={indicator?.height} onChange={(val) => updateObject('indicator', 'height', val)} units={[pxUnit(10), emUnit(1)]} isResetValueOnUnitChange={true} />

                            <UnitControl className='mt20' label={__('Radius:', 'business-review')} labelPosition='left' value={indicator.radius}
                                onChange={val => updateObject('indicator', 'radius', val)} units={[pxUnit(10), perUnit(100)]} isResetValueOnUnitChange={true} />

                            <BorderControl className='mt20' label={__('Active Border', 'business-review')} value={indicator?.activeBorder} onChange={(val) => updateObject('indicator', 'activeBorder', val)} />


                        </PanelBody>}
                    </div>
                </>}
                {"patterns" === tab.name && <>

                    {!isPremium && <p className='noticePatter'>{__('it\'s only available on pro version', 'slider')}</p>}
                    {patterns.map((pattern, index) => {
                        const imgName = pattern.name.replace('/', '-');
                        const imgPath = bsbInfo.patternsImagePath + imgName + '.png';
                        return <div key={index} className='bsbPatters'>
                            <img key={index} style={{ width: '100%' }} src={imgPath} />
                            <div className="overlyButton">
                                <button onClick={() => {
                                    if (isPremium) {
                                        const blocks = parse(pattern.content);
                                        wp.data.dispatch('core/block-editor').replaceBlock(props.clientId, blocks);
                                    } else {
                                        setProModalOpen(true);
                                    }
                                }}>{__('Apply', 'slider')}</button>
                            </div>
                        </div>
                    })}
                </>}
            </>}
        </TabPanel>
    </InspectorControls >
        <ProModal setProModalOpen={setProModalOpen} proModalOpen={proModalOpen} />
    </>
}
export default Settings;